








































// Import Bulma's core
@import "~bulma/sass/utilities/_all";

// Set your colors
$primary: #ffca1d;
$primary-invert: findColorInvert($primary);
$twitter: #4099FF;
$twitter-invert: findColorInvert($twitter);

// Setup $colors to use as bulma classes (e.g. 'is-twitter')
$colors: (
    "white": ($white, $black),
    "black": ($black, $white),
    "light": ($light, $light-invert),
    "dark": ($dark, $dark-invert),
    "primary": ($primary, $primary-invert),
    "info": ($info, $info-invert),
    "success": ($success, $success-invert),
    "warning": ($warning, $warning-invert),
    "danger": ($danger, $danger-invert),
    "twitter": ($twitter, $twitter-invert)
);

// Links
$link: $primary;
$link-invert: $primary-invert;
$link-focus-border: $primary;

// $menu-item-color: $text !default;
// $menu-item-radius: $radius-small !default;
// $menu-item-hover-color: $text-strong !default;
// $menu-item-hover-background-color: $background !default;
// $menu-item-active-color: $link-invert !default;
// $menu-item-active-background-color: $link !default;

$menu-list-border-left: 1px solid $border !default;

$menu-label-color: $text-light !default;

$section-padding: 0rem 0rem;

// Import Bulma and Buefy styles
@import "~bulma";
@import "~buefy/src/scss/buefy";


html {
  background: #6c89b5;
  background: -ms-linear-gradient(top, #6c89b5 0%, #144794 100%);
  background: -moz-linear-gradient(top, #6c89b5 0%, #144794 100%);
  background: -o-linear-gradient(top, #6c89b5 0%, #144794 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0, #6c89b5), color-stop(1, #144794));
  background: -webkit-linear-gradient(top, #6c89b5 0%, #144794 100%);
  background: linear-gradient(to bottom, #6c89b5 0%, #144794 100%);
}

body {margin: 0;  /*removes default style*/
  padding: 0;
  height: 100%;
}

#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  height: 100%;
  width: 100%;
  // color: #2c3e50;
}

.menu {
  font-size: $size-normal;
  // Sizes
  &.is-small {
    font-size: $size-small;
  }
  &.is-medium {
    font-size: $size-medium;
  }
  &.is-large {
    font-size: $size-large;
  }
}

.menu-list {
  line-height: 1.25;
  a {
    border-radius: $menu-item-radius;
    color: $menu-item-color;
    display: block;
    padding: 0.5em 0.75em;

    &:hover {
      background-color: $menu-item-hover-background-color;
      color: $menu-item-hover-color;
    }

    // Modifiers
    &.is-active {
      background-color: $menu-item-active-background-color;
      color: $menu-item-active-color;
    }
  }

  li {
    ul {
      border-left: $menu-list-border-left;
      margin: 0.75em;
      padding-left: 0.75em;
    }
  }
}

.menu-label {
  color: $menu-label-color;
  font-size: 0.75em;
  letter-spacing: 0.1em;
  text-transform: uppercase;

  &:not(:first-child) {
    margin-top: 1em;
  }

  &:not(:last-child) {
    margin-bottom: 1em;
  }
}

.section.main {
  padding-right: 1.5rem;
}

.content {
  padding: 0;
  margin-top: 30px;
  padding: 20px;
  min-height: 640px;

  & .card {
    border-radius: 8px;
    padding: 16px;
    box-shadow: 0 2px 4px #000000ad;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

