


























.menu {
    background: #0000008c;
    box-shadow: 2px 0 0px #0000008c;
    height: 100%;
    padding-top: 40px;
    
    & #nav {

        & a {
            color: white;
            transition: 0.2s;

            &:hover {
                background: #00a2ff;
                color: rgb(230, 230, 230);
            }    
            
        }
    }
    
}

.menu-label {
    color: white!important;
}
